.content {
    max-width: 800px;
    margin: 0 auto;
}

.content p {
    font-size: 1em;
    line-height: 1.6;
    color: #333;
}

.content p strong {
    font-weight: bold;
}

.subtle-link {
    color: #666;
    text-decoration: none;
}

.subtle-link:hover {
    text-decoration: underline;
}
